import { Component, OnInit } from '@angular/core';
import {
  INgxChatUiMessage,
  INgxChatUiMessageType,
  INgxChatUiMessagePayload,
  INgxChatUiMessagePartner,
  INgxChatUiState
} from '@ngx-chat-ui/libs/ngx-chat-ui';

@Component({
  selector: 'ngx-chat-ui-demo-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  partners: INgxChatUiMessagePartner[] = [];
  messages: {
    [chatKey: string]: INgxChatUiMessage[]
  } = {
    first: [],
    second: [],
    third: []
  };
  state: {
    [chatKey: string]: INgxChatUiState
  } = {
    first: {},
    second: {},
    third: {}
  };

  ngOnInit() {
    this.partners = this.partners.concat([
      {
        messagePartnerId: 'me',
        firstName: 'Sergey',
        lastName: 'Kalaus'
      },
      {
        messagePartnerId: 'it',
        avatar: 'data:image/jpeg;base64,'
      }
    ]);

    this.messages.first = this.messages.first.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '1',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.select,
          items: [
            {
              icon: '👌',
              id: 0,
              className: 'ngx-chat-ui-action-select-item-cta'
            },
            {
              text: 'No',
              id: 1
            }
          ]
        }
      },
    ]);

    this.messages.second = this.messages.second.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '2',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.text
        }
      },
    ]);

    this.messages.third = this.messages.third.concat([
      {
        date: new Date(),
        messagePartnerId: 'me',
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Hello'
        }
      },
      {
        date: new Date(),
        messageId: '1',
        messagePartnerId: 'it',
        isIncoming: true,
        payload: {
          type: INgxChatUiMessageType.text,
          text: 'Wanna play?'
        },
        action: {
          type: INgxChatUiMessageType.select,
          className: 'ngx-chat-ui-action-select-list',
          items: [
            {
              text: 'Yes',
              id: 0,
              className: 'ngx-chat-ui-action-select-item-cta'
            },
            {
              text: 'No',
              id: 1
            }
          ]
        }
      },
    ]);
  }

  onResponse(chatKey: string, payload: INgxChatUiMessagePayload) {
    setTimeout(() => {
      this.messages[chatKey] = this.messages[chatKey].concat([
        {
          date: new Date(),
          messagePartnerId: 'me',
          payload
        }
      ]);
    });
  }
}
